const JsyServer = require("../../../services/jsy-server.js");
let _this,
   _buyOrSell,
    type = 0,
    adding = true;

export default {
  data() {
    return {
      selectContent: [],
      bindSelect: false,
      //是否点开搜素类别
      list: [],
      optionIndex: 0,
	  idList:[]
    };
  },

  onPullDownRefresh: function () {
    this.getDeputyList();
  },
  onLoad: function (options) {
    adding = true;
    _this = this;
    _this.idList = JSON.parse(options.optionList)
	_buyOrSell= JSON.parse(options.buyOrSell)
    console.log("options===",_this.idList)
    // type = options.type || 0;
    this.getDeputyList();
    
  },
  components: {},
  props: {},
  methods: {
    // 获得区域列表
    getAreaList: function () {
      JsyServer.getAreaList().then(res => {
        let selectContent = [{
          name: "全部区域",
          id: false
        }].concat(res.data);
        console.log("selectContent", selectContent);
        this.setData({
          selectContent: selectContent
        });
      }).catch(err => {
        console.log("err=>", err);
      });
    },
    // 获取区域经理列表
 //    getManagerList: function (data) {
		
 //      JsyServer.managerList(data).then(res => {
 //         console.log("list===",res)
 //        _this.list = res.data.data.list

 //        console.log("res---", res);
 //      }).catch(err => {
 //        console.log("err---", err);
 //      });
	
 //    },
   getDeputyList:function(){
	  
	   let url = this.Api.deputyList
	   this.myRequest({},url,'get').then(res => {
		     console.log('bangban',res)
             _this.list = res.data.data.list 
	    }).catch(err => {
	      console.log("getBSList=err==", res);
	    });  
   },
    bindSearch: function (e) {
      let content = e.detail.value;
      let areaid = this.selectContent[0].id;
      let data = {
        content
      };

      if (areaid) {
        data.areaid = areaid;
      }

      this.getManagerList(data);
    },
    // 点击选项框
    tapBindSelect: function () {
      this.setData({
        bindSelect: !this.bindSelect
      });
    },
    // 点击选项框中的选项(客户列表)
    bindSelectContent: function (e) {
      let index = e.currentTarget.dataset.index;
      let selectContent = this.selectContent;
      let obj = selectContent[0];
      selectContent[0] = selectContent[index];
      selectContent[index] = obj;
      this.setData({
        selectContent: selectContent
      });
      if (index == 0) return;

      if (selectContent[0].id) {
        this.getManagerList({
          areaid: selectContent[0].id
        });
      } else {
        this.getManagerList({});
      }
    },
    tapPitchOn: function (e) {
      let index = e.currentTarget.dataset.index;
      this.setData({
        optionIndex: index
      });
	  console.log(this.list[this.optionIndex])
    },
	 // 分配客户
    confirm: function () {
      let _data= {
      		  companyCodes: this.idList,	//客户编码数组
      		  userPostCode: this.list[this.optionIndex].userCode,		// 帮办用户编码
      		 
      }
      JsyServer.managerAllot(_data).then(res => {
		  console.log('帮办分配',res)
        if (res.data.status == 0){
			    
      			wx.showToast({
      			  title: '分配成功'
      			});
				var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面
				//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				prevPage.setData({
						isDoRefresh:true
				})
      			let timer = setTimeout(() => {
      			  wx.navigateBack({});
      			}, 500);
      		}
       
      }).catch(err => {
        adding = true;
        wx.showToast({
          title: '分配失败',
          icon: 'none'
        });
      });
    },

   
   
  
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
